@import url(https://fonts.googleapis.com/css2?family=Lato&family=Mada:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mada', sans-serif;
  cursor: default;
}

body {
  background-color: hsl(225, 13%, 13%);
}
.employees {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  width: 100%;
  overflow-x: scroll;
  margin-top: 20px;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.employees > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: hsl(0, 0%, 95%);
  width: 200px;
  height: 100%;
  margin-right: 20px;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  padding: 10px;
  padding-bottom: 0;
  border: 2px solid transparent;
}

.employees > div:hover {
  position: relative;
  z-index: 1000;
  border: 2px solid hsl(23, 100%, 75%);
}

.employees-by-letter {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  overflow-y: scroll;
}

.employees-by-letter > div {
  margin-bottom: 20px;
}

.employees-by-letter > div:last-child {
  margin-bottom: 0px;
}

h3 {
  font-size: 26px;
  margin-bottom: 20px;
}

h4 {
  font-size: 20px;
}

.active {
  color: hsl(23, 100%, 50%);
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

label {
  text-transform: capitalize;
  font-size: 16px;
  position: relative;
  padding-left: 20px;
}

label:after {
  content: '';
  width: 10px;
  height: 10px;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-55%);
          transform: translateY(-55%);
  border-radius: 10px;
  border: 2px solid hsl(23, 100%, 75%);
  background-color: hsl(0, 0%, 95%);
}

label:hover:after {
  border: 2px solid hsl(23, 100%, 50%);
}

input {
  display: none;
}

.selected:after {
  border: 2px solid hsl(23, 100%, 50%);
  background: radial-gradient(hsl(23, 100%, 50%) 30%, hsl(0, 0%, 95%) 50%);
}

#selected-letter {
  border: 2px solid hsl(23, 100%, 50%);
}

.right-side > h2 {
  margin-top: 20px;
}

.birthday-months {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  width: 100%;
  overflow-y: scroll;
  margin-top: 20px;
}

.birthday-months > div {
  background-color: hsl(0, 0%, 100%);
  width: 100%;
  margin-bottom: 20px;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  padding: 10px;
}

.birthday-months > div:last-child {
  margin-bottom: 0px;
}

.birthday-months > div > h2 {
  margin-bottom: 5px;
}

.birthday-months > div > div > div {
  margin-left: 15px;
  position: relative;
}

.birthday-months > div > div > div > span {
  font-weight: bold;
}

.birthday-months > div > div > div:before {
  content: '';
  display: block;
  height: 7px;
  width: 7px;
  border-radius: 7px;
  background-color: hsl(23, 100%, 50%);
  position: absolute;
  left: -15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.main-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 30px;
  height: calc(100vh - 80px);
}

header > h2 {
  color: white;
  height: 50px;
  padding: 10px 30px;
  position: relative;
}

footer {
  color: white;
  padding: 5px 30px 0px;
}

header > h2:after {
  background-color: hsl(23, 100%, 50%);
  height: 5px;
  width: 5px;
  border-radius: 3px;
  content: '';
  position: absolute;
  z-index: 100;
  display: block;
  top: 15px;
  left: 217px;
}

section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  padding: 20px;
}

section > div {
  -webkit-align-self: center;
          align-self: center;
}

h1 {
  font-size: 40px;
  border-bottom: 5px solid hsl(23, 100%, 50%);
}

.left-side {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  min-width: 680px;
  background-color: hsl(0, 0%, 100%);
}

.left-side > h1 {
  background-image: url(/employeesBirthday/static/media/fon.73b0edf9.png);
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: bottom;
}

.right-side {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: hsl(0, 0%, 95%);
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

